function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '1',
        spaceBetween: 32,
        effect: "fade",
        loop: true,
        speed: 1000,
        autoplay: {
            delay: 5000,
            disableOnInteraction: false
        },
    })
}


$(document).ready(function() {
    sliderMain();


    //Menu
    $('.burger').click(function () {
        $('.header-menu').addClass('header-menu--active')
        $('body').addClass('fixed-body')
    })

    $('.header-menu__close').click(function () {
        $('.header-menu').removeClass('header-menu--active')
        $('body').removeClass('fixed-body')
    })


    //Animation
    gsap.registerPlugin(ScrollTrigger);

    //Main
    const circles = gsap.timeline({defaults: {duration: 0.6}})
    circles.from('.circle', {opacity: 0, x: 20})
        .from('.circle2', {opacity: 0, x: 20})
        .from('.circle3', {opacity: 0, x: 20})
        .from('.circle4', {opacity: 0, x: 20})
        .from('.circle5', {opacity: 0, x: 20})

    //Info
    const item = gsap.timeline({
        scrollTrigger: {
            trigger: '.info__item',
            start: 'top 100%',
            end: 'top 30%',
        },
        defaults: {duration: 0.6},
    })
    
    item.from('.info__item-1', {opacity: 0, y: -200})
    .from('.info__item-2', {opacity: 0, y: -200})
    .from('.info__item-3', {opacity: 0, y: -200})
    .from('.info__item-4', {opacity: 0, y: -200})
    .from('.info__item-5', {opacity: 0, y: -200})

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})